import React,{useState} from 'react';
import { Row, Col, Layout, Typography,Button,Space, Switch } from 'antd';
import { LeftOutlined,InfoCircleOutlined } from '@ant-design/icons';
import {useHistory} from 'react-router-dom';
import { useStore } from 'react-redux';
import { useWindowSize } from '../../hooks/useWindowSize';



export const EditProfile:React.FC = () => {
    const state = useStore().getState().UserReducer;
    const [form,setForm] = useState({
        firstname:state.profileObj.name,
        lastname:state.profileObj.givenName,
        email:state.profileObj.email,
        phone:'',
        adress:'',
        country:'',
        rescountry:'',
        personalcode:'',
        pep:false,
    })
    const resize = useWindowSize();
    const {Content}  = Layout;
    const {Title,Paragraph} = Typography;
    const history = useHistory()
    const goBack = () => {
        history.goBack();
    }
    const sbmeditbtn = (event:React.FormEvent) => {
        event.preventDefault();
    }
    const changeInputEditForm = (event:any) => {
        const {name,value}:any = event.target;
        setForm({...form,[name]:value});
       
    }
    
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1 padding-bottom-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:'start'}}>
                        <Button icon={<LeftOutlined />} className='dark-blue body-line-normal-w-200' type='text' onClick={goBack} >{resize === false ?'Back':'Edit Profile'}</Button>
                    </Col>
                    <Col xxl={24} style={resize === false ? {textAlign:'center'}:{textAlign:'center',maxWidth:"480px"}} className="padding-top-2">
                        <Space direction='vertical'>
                            {resize === false ?
                                <Title className='medium-line-medium-w-500 dark-blue padding-bottom-2'>
                                    Edit Profile
                                </Title> : null
}
                                <form onSubmit={sbmeditbtn}>
                                    <Space direction='vertical'size={21}>
                                    <div className="group">
                                        <input  type="text" name='firstname' disabled value={form.firstname} onChange={changeInputEditForm} required />
                                        <label>First name</label>
                                        
                                    </div>
                                    <div className="group">
                                        <input type="text" name='lastname' disabled value={form.lastname} onChange={changeInputEditForm} required />
                                        <label>Last name</label>
                                        
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.email} disabled name='email' onChange={changeInputEditForm} required/>
                                        <label>Email</label>
                                        
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.phone} name='phone'  onChange={changeInputEditForm} required/>
                                        <label>Phone number</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.adress} name='adress' onChange={changeInputEditForm} required />
                                        <label>Address</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.country} disabled name='country' onChange={changeInputEditForm} required/>
                                        <label>Country</label>
                                        
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.rescountry} name='rescountry'  onChange={changeInputEditForm} required/>
                                        <label>Resident country</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" value={form.personalcode} disabled name='personalcode' onChange={changeInputEditForm} required/>
                                        <label>Personal code</label>
                                        
                                    </div>
                                    <Row justify='space-between' className='padding-right-2 padding-left-2 padding-top-1 padding-bottom-1'>
                                        <Paragraph  className='dark-blue body-line-normal-w-300'>
                                            PEP
                                            <InfoCircleOutlined className='offset-left-1'/>
                                        </Paragraph>
                                       
                                        <Switch onChange={()=>setForm({...form,pep:!form.pep})}  />
                                    </Row>
                                    <button type='submit' className='requestbtn'>Save changes</button>
                                    
                                    </Space>
                                </form>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}