import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import { Row,Col,Layout,Space,Button, Typography,DatePicker } from 'antd';
import {useHistory} from 'react-router-dom';
import { LeftOutlined,DownOutlined  } from '@ant-design/icons';
import './added.scss';
import { useWindowSize } from '../../hooks/useWindowSize';
export const AddedAdditional:React.FC = () => {
    const resize = useWindowSize();
    const [additional,setAdditional]:any = useState({
        information:'',
        date:''
    });
    const handleChangeAdditionalInput = (event:any) => {
        console.log(event.target);
        const {name,value}:any = event?.target;
        setAdditional({...additional,[name]:value})
    }
    const {Content} = Layout;
    const {Title} = Typography;
    const history = useHistory();
    const goBackBtn = () => {
        history.goBack();
    }
    const addAdditionalSbm =(e:React.FormEvent)=>{
        e.preventDefault();
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:'start'}}>
                        <Button icon={<LeftOutlined/>} className='dark-blue body-line-normal-w-200' type='text' onClick={goBackBtn}>{resize === false ? 'Back':'Add additional'}</Button>
                    </Col>
                    <Col xxl={24} style={resize === false ? {textAlign:"center"}:{textAlign:"center",maxWidth:'480px'}} className="padding-top-2">
                        <Space direction='vertical'>
                            {resize === false ? <Title className='medium-line-medium-w-500 dark-blue padding-bottom-5'>Add additional</Title>:null}
                            <form onSubmit={addAdditionalSbm}>
                                <Space direction='vertical' size={21} >
                                <div className="group">
                                    <input type="text" className='dropdown' name='information' onChange={handleChangeAdditionalInput} required />
                                    <label>Additional information</label>                           
                                </div>
                                <div className='padding-bottom-3'>
                                    <DatePicker 
                                    name='date' 
                                    onChange={(date,dateString)=>setAdditional({...additional,date:dateString})}  
                                    popupStyle={{width:"480px",borderRadius:'21px'}} 
                                    placeholder="Choose date" 
                                    className='body-line-normal-w-300 group' 
                                    style={{width:"480px",height:"69px",padding:"0",borderRadius:"40px"}}  
                                    
                                    />
                                
                                   
                                </div>
                                <button className='requestbtn' type='submit'>Add</button>
                                </Space>
                            </form>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}