import { ActionsType,AdditionalType } from "../types";
const initialState:any = [];


const AdditionalReducer = (state = initialState,action:AdditionalType) => {
    switch(action.type){
        case ActionsType.ADD_ADDITIONAL:
           return [...state,{...action.payload}];
        case ActionsType.ALL_ADDITIONAL:
            return state;
        default:
        return state;
    }
}

export default AdditionalReducer;