import React,{useEffect} from 'react';
import {Row,Layout} from 'antd';
import { useTranslation } from 'react-i18next';
import { ResizeGlobalContext } from '../../context/screen';
import {useHistory} from 'react-router-dom';
import './dealsmsg.scss';
interface Props{
    error?:boolean
}

export const DealsMsg:React.FC<Props> = ({error = false}) => {
    const {t,i18n} = useTranslation();
    const history = useHistory();
    useEffect(()=>{},[t]);
    return (
        <div className={`dealsmsg-container ${error === false ? "approve":"error"}`}>
            <div className='deals-msg-text'>
                {error === false ? t("dealsmsg.approve.text") : t("dealsmsg.error.text")}
            </div>
            <div className='deals-msg-btn'>
                <button>{error === false ? t("dealsmsg.approve.btn") : t("dealsmsg.error.btn")}</button>
            </div>
        </div>
    )
}