import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import {useSelector} from 'react-redux';
import {Row, Col,Card, Select, Space} from 'antd';
import { DealListProps } from '../../Types';
import './deallist.scss';
import {MenuOutlined,SearchOutlined} from '@ant-design/icons';
import { DealsMsg } from '../dealsmsg';
import { useHistory } from 'react-router';


export const DealList:React.FC<DealListProps> =({data,background})=>{
    const history = useHistory();
    const state = useSelector((state:any) => state.AdditionalReducer);
    const [direction,setDirection] = useState({
        vertical:true,
        horizontal:false
    })
    const {resize} = ResizeGlobalContext();
    const changePositionIconVertical = () => {
            setDirection({vertical:true,horizontal:false});
            
    }
    const changePositionIconHorizontal = () => {
            setDirection({vertical:false,horizontal:true});
    }
    const {Meta} = Card;
    const {Option} = Select;
    const current = (id:string)=>{
        history.push(`/deals/${id}`)
    }
        return (<Row justify='center' className={`${resize === false ? 'offset-top-2 padding-left-1 padding-right-1 padding-top-1' : 'padding-top-1 padding-right-1 padding-left-1' } `}>
            <Col xxl={16} sm={16} xl={16} xs={24} style={{maxWidth:"872pt",width:"100%"}}>
                <Space direction='horizontal' style={{display:"flex",justifyContent:"space-between"}}>
                <div className='medium-line-medium-w-600 dark-blue'>
                    Deals
                </div>
                <div>
                    <SearchOutlined style={{width:'32px'}}  className='searchicondeals' />
                </div>
                </Space>
            </Col>
            <Col xxl={16} xs={24}>
                <DealsMsg />
            </Col>
            <Col className="padding-top-2" xxl={16} sm={24} xl={24} xs={24} style={{textAlign:"center",maxWidth:"872pt",width:"100%"}} >
                <Row justify="space-between" className='padding-bottom-2'>
                <Col xxl={24} xs={24} style={{display:"flex",justifyContent:'space-between'}}>
                <div>
                    <Select dropdownClassName="dark-blue body-line-normal-w-500" defaultValue="View all" className="dark-blue body-line-normal-w-200" bordered={false}>
                        <Option value="View all"> View all</Option>
                    </Select>
                </div>
                <div style={{cursor:'pointer'}}>
                    <MenuOutlined className='iconposition offset-right-1' onClick={changePositionIconVertical}  style={direction.vertical === true ? {color:'#01223A',width:'21px'} : {color:'#707070',width:"21px"}} />
                    <MenuOutlined className='iconposition' onClick={changePositionIconHorizontal} rotate={270} style={direction.horizontal === true ? {color:'#01223A',width:'21px'}:{color:'#707070',width:'21px'}} />
                </div>
                </Col>
                </Row>
                {/* <div style={resize === false ? {display:'flex'} : {display:'flex',flexDirection:'column',...direction.vertical === false ? {flexDirection:'row',flexWrap:'wrap'} : null}}>
                    {state && (
                        state.map((e:any,index:any)=>(
                            <Card className='card-deals' bordered={false} hoverable style={direction.vertical === true ? {display:'flex',justifyContent:"space-between",marginRight:"1em", ...resize === true ? {margin:'1em 0',width:'100%',justifyContent:'flex-start'} :null} : {marginRight:"1em", ...resize === true ? {margin:'1.3em',width:'42%'}:null}} cover={<img alt="winwind" src={e.photo} />}>
                                <Meta title={e.product} description={e.product} />
                                <p className='status-card'>{e.status}</p>
                            </Card>
                        )))
                    }

                </div> */}
                <div className={`card-container ${direction.vertical === true ? "vertical" :"horizontal"} `}>
                        {state && (
                            state.map((e:any,index:any)=>(
                                <div key={index} className="card-item" onClick={()=>current(e.id)}>
                                    <div className="card-item-image">
                                        <img src={e.photo} alt="" />
                                    </div>
                                    <div className="card-item-text">
                                        <p className='title'>{e.role}</p>   
                                        <p className='description'>{e.product}</p>
                                        <p className='status'>{e.status}</p>
                                        <p className="name">John Smith</p>
                                    </div>
                                </div>
                            ))
                        )}
                </div>
            </Col>
        </Row>
    )
}


