import { ActionCreator,Dispatch } from "redux";
import { ThunkAction } from "redux-thunk";
import { ActionsType,AddedInformation } from "../types";


export const AddedAdditional:ActionCreator<ThunkAction<Promise<any>,null,null,AddedInformation>> = (data)=>{
    return async (dispatch:Dispatch)=>{
        dispatch({
            type:ActionsType.ADD_ADDITIONAL,
            payload:data
        })
    }
}
