import { ActionCreator,Dispatch } from "redux";
import { ThunkAction } from "redux-thunk";
import { ActionsType,AllAdditional } from "../types";


export const AllAdditionalAction:ActionCreator<ThunkAction<Promise<any>,null,null,AllAdditional>>=()=>{
    return async (dispatch:Dispatch)=>{
        dispatch({
            type:ActionsType.ALL_ADDITIONAL,
        })
    }
}