import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import {useHistory} from 'react-router-dom';
import {Row,Col,Typography,Button,Space,Avatar,Divider,Collapse} from 'antd';
import card from '../../assets/img/icons/ic_card.png';
import invite from '../../assets/img/icons/ic_invite.png';
import flag from '../../assets/img/icons/flag.png';
import './profile.scss';
import { useStore } from 'react-redux';
export const Profile:React.FC = () => {
    const state = useStore().getState().UserReducer;
    
    const {resize} = ResizeGlobalContext();
    const history = useHistory();
    const {Title} = Typography;
    const {Panel} = Collapse;
    const goToEditProfile = () => {
        history.push('/edit/profile');
    }
    const goToPaymentBtn = () => {
        history.push('/edit/payment');
    }
    const goToLanguageBtn = () => {
        history.push('/edit/language');
    }
    const goInviteFriendBtn = () => {
        history.push('/edit/invite');
    }
    return (
        <>
        <Row justify='center' className={`${resize === false ? 'padding-top-2' : 'padding-top-1'} padding-bottom-3 padding-left-1 padding-right-1 profilebg`}>
            
                    
                    <Col xxl={16} sm={16} xl={16} xs={24}>
                        
                    <div className='medium-line-medium-w-600 dark-blue'>
                        Profile
                    </div>
                    
                    </Col>
                    <Col xxl={16} sm={16} xl={16} xs={24} className='offset-top-2' style={resize === false ? {} : {textAlign:'center'}}>
                        <Space direction={resize === false ? `horizontal` : "vertical"}>
                            <Avatar size={210} src={state.profileObj.imageUrl} />
                            <Space direction='vertical' className={`${resize === false ? 'offset-left-2': "offset-top-1"}`}>
                            <Title className='dark-blue normal-line-medium-w-200'>{state.profileObj.name}</Title>
                            <Button onClick={goToEditProfile} style={resize === false ?{paddingLeft:0}:{}} className='body-line-normal-w-200' type='link'>Edit profile</Button>
                            </Space>
                        </Space>
                    </Col>
                   
            
           
        </Row>
        <Row justify='center' className='padding-top-2'>
            <Col xxl={16} sm={16} xl={16} xs={24}>
                    <Collapse onChange={goToPaymentBtn}  style={{fontSize:"18pt",lineHeight:"24pt", fontFamily:"'DM Sans', sans-serif",fontWeight:300}} ghost={true} expandIconPosition="right" bordered={false} accordion={false} expandIcon={()=><img alt='' src={card} />}>
                        <Panel  key='1' header='Payment information'>

                        </Panel>
                    </Collapse>
                    <Divider style={{marginBottom:'0px',marginTop:"33px"}}/>
                    <Collapse onChange={goToLanguageBtn}  style={{fontSize:"18pt",lineHeight:"24pt", fontFamily:"'DM Sans', sans-serif",fontWeight:300}} ghost={true} expandIconPosition="right" bordered={false} accordion={false} expandIcon={()=><img alt='' src={flag} />}>
                        <Panel  key='1' header='Language'>

                        </Panel>
                    </Collapse>
                    <Divider style={{marginBottom:'0px',marginTop:"33px"}}/>
                    <Collapse onChange={goInviteFriendBtn} style={{fontSize:"18pt",lineHeight:"24pt", fontFamily:"'DM Sans', sans-serif",fontWeight:300}} ghost={true} expandIconPosition="right" bordered={false} accordion={false} expandIcon={()=><img alt='' src={invite} />}>
                        <Panel  key='1' header='Invite friend'>

                        </Panel>
                    </Collapse>
                    <Divider style={{marginBottom:'0px',marginTop:"33px"}}/>
            </Col>
        </Row>
        </>
    )
}