import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import {NavLink,useHistory} from 'react-router-dom';
import {Image,Button} from 'antd';
import logo from '../../assets/img/icons/logo_horizontal.png';
import { PlusOutlined,BarsOutlined, UserOutlined, RiseOutlined, QuestionOutlined} from '@ant-design/icons';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Header:React.FC = () => {
    const resize = useWindowSize();
    const history = useHistory();
    const newDealBtn = () => {
        history.push('/create_deal');
    }

    return (
        <header>
            <div className='logo'>
                <Image src={logo} preview={{visible:false, mask:false}} />
            </div>
            <nav>
                <div className='nav'>
            <NavLink exact to='/deals/deallist'>
                <span className='icon_text'>
                {resize === false ? null :<BarsOutlined />}
                Deals
                </span>
            </NavLink>
            <NavLink to='/deals/help'>
                <span className='icon_text'>
                    {resize === false ? null : <QuestionOutlined />}
                Help
                </span>
            </NavLink>
            </div>
            {resize === false ? null : <div className='nav'><button onClick={newDealBtn} className='mobilebtn'><PlusOutlined/></button></div>}
            <div className='nav'>
            <NavLink to='/deals/profile'>
                <span className='icon_text'>
                    { resize === false ? null : <UserOutlined />}
                Profile
                </span>
            </NavLink>
            <NavLink to='/deals/raiting'>
                <span className='icon_text'>
                {resize === false ? null : <RiseOutlined />}
                Raiting
                </span>
            </NavLink>
            </div>
            </nav>
            <div className='desktopbtn'>
                <Button onClick={newDealBtn} icon={<PlusOutlined style={{fontSize:"22px"}}/>} className='btn-deals' >New deal</Button>
            </div>
        </header>
    )
}