import { ActionCreator,Dispatch } from "redux";
import { ThunkAction } from "redux-thunk";
import { ActionsType, SigningUser } from "../types";


export const UserAction:ActionCreator<ThunkAction<Promise<any>,null,null,SigningUser>> = (data) => {
    return async (dispatch:Dispatch) => {
        dispatch({
            type:ActionsType.SIGNING_USER,
            payload:data
        })
    }
}