import React, {useState} from 'react';
import { Row, Col, Layout, Typography,Button,Space } from 'antd';
import { LeftOutlined } from '@ant-design/icons';
import {useHistory} from 'react-router-dom';
import { useWindowSize } from '../../hooks/useWindowSize';
export const WriteUs:React.FC =() => {
    const resize = useWindowSize();
    const [message,setMessage] = useState({name:"",email:"",messages:''});
    const history = useHistory();
    const goBack = () => {
        history.goBack();
    }
    const {Content} = Layout;
    const {Title} = Typography;
    const handleMessageChange = (event:any) => {
        const {name,value} = event.target;
        setMessage({...message,[name]:value});
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:"start"}}>
                    <Button icon={<LeftOutlined />} className='dark-blue body-line-normal-w-200' type='text' onClick={goBack} >{resize === false ? 'Back' : 'Write to us' }</Button>
                    </Col>
                    <Col xxl={24} style={resize === false ? {textAlign:'center'}:{textAlign:'center',maxWidth:"480px"}} className='padding-top-2'>
                        <Space direction='vertical'>
                            { resize === false ?
                            <Title className='medium-line-medium-w-500 dark-blue padding-bottom-2'>
                                Write to us
                            </Title> : null
}
                            <form>
                                <Space direction='vertical' size={21}>
                                    <div className="group">
                                        <input type="text" required/>
                                        <label>Name</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required/>
                                        <label>Email</label>
                                    </div>
                                    <div className="group">
                                        <input type="text" required/>
                                        <label>Message</label>
                                    </div>
                                    <button className='requestbtn offset-top-3'>Send message</button>
                                </Space>
                            </form>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}