import React,{useEffect,useState} from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import {Layout,Row,Col,Typography,Rate,Image} from 'antd';
import closeIcon from '../../assets/img/icons/ic_close.png';
import './review.scss';

export const Review:React.FC = () => {
    const [review,setReview] = useState({message:'',rate:0});
    const history = useHistory();
    const {t} = useTranslation();
    useEffect(()=>{},[t]);
    const {Content} = Layout;
    const {Title, Paragraph} = Typography;
    const changeReviewInput = (event:any) => {
        const {name,value} = event.target;
        setReview({...review,[name]:value});
    }
    const rateChange = (value:any) => {
        setReview({...review,rate:value})
    }
    const submitForm = (event:React.FormEvent) => {
        event.preventDefault();
    }
    const nextStep = () => {
        history.push('/review');
    }
    const goBack = () => {
        history.goBack();
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-left-1 padding-right-1 padding-bottom-1 padding-top-1' style={{height:'100vh'}}>
                <Row justify='center' >
                    <Col xxl={24} xs={24} className='offset-bottom-3'  style={{textAlign:'end'}}>
                        <Image onClick={goBack} src={closeIcon} preview={{visible:false,mask:false}} style={{cursor:'pointer'}} />
                    </Col>
                    <Col xxl={24} style={{textAlign:'center',alignItems:'center',display:'flex',justifyContent:'center',flexDirection:'column',gap:'27px'}}>
                        
                            <Title>{t('review.title')}</Title>
                            <Paragraph>{t('review.paragraph')}</Paragraph>
                            <Rate onChange={rateChange} allowHalf className='orange'/>
                            <Paragraph> Tap to rate </Paragraph>
                            <form onSubmit={submitForm}>
                                <div className="group">
                                    <input type="text" value={review.message} onChange={changeReviewInput} required name='message' />
                                    <label>Review (optional)</label>
                                </div>
                            </form>
                            <button onClick={nextStep} className='send-review-btn'>{t('review.btn')}</button>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}