import React,{useEffect} from 'react';
import { MessagesProps } from '../../Types';
import { useTranslation } from 'react-i18next';
import {Layout,Row,Col,Image} from 'antd';
import { useHistory } from 'react-router-dom';
import './messages.scss';
export const Messages:React.FC<MessagesProps> = ({background,type,options,urlBtn}) => {
    const history = useHistory();
    const {t} = useTranslation();
    useEffect(()=>{},[t]);
    const {Content} = Layout;
    const goTo = (e:any) => {
        if(typeof e !== 'string' || e === undefined){
            history.goBack();
        }else{
            history.push(e);
        }
    }
    return (
        <Layout className={background}>
            <Content>
                <Row justify='center' style={{height:'100vh'}}>
                    <Col xxl={24} style={{textAlign:'center',alignItems:'center',display:'flex',justifyContent:'center'}}>
                  
                        <div className="message-conteiner">
                            <div className="message-image">
                                {options.visible === true ?<Image src={options.src} preview={{visible:false, mask:false}} /> : null}
                            </div>
                            <div className="message-title">
                                {t(`messages.${type}.title`)}
                            </div>
                            <div className="message-paragraph">
                                {t(`messages.${type}.paragraph`)}
                            </div>
                            <div className="message-btn">
                            <button onClick={()=>goTo(urlBtn)} className={`${type}-btn`}>{t(`messages.${type}.btn`)}</button>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}