import React,{Suspense} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import { useHistory,useParams } from 'react-router';
import {Row,Col,Button,Steps} from 'antd';
import './current.scss';
import { useStore } from 'react-redux';
import { LeftOutlined } from '@ant-design/icons';


export const CurrentDeal:React.FC = () => {
    const history = useHistory();
    const {resize} = ResizeGlobalContext();
    const store = useStore().getState().AdditionalReducer;
    const params:any = useParams()
    const current = store.filter((n:any)=>{return n.id === params.id})[0];
    const goBack = () => {
        history.goBack();
    }
   const {Step} = Steps;
   const steps =[{
       title:"Deal information sent to buyer"
   },
{
    title:"Deal has been approved"
},
{
    title:"The buyer paid a deposit"
},
{
    title:"The seller is fulfilling the order"
},
{
    title:"The seller has fulfilled the order"
},
{
    title:"Lorem ipsum dolor "
},
{
    title:"Excepteur sint occaecat  "
}]
    return (
        <Suspense fallback={<div></div>}>
        <Row justify="center" className='current_deal'>
            <Col xxl={13} sm={16} xl={16} xs={24}>
                <Button className='current_deal_btn' onClick={goBack} type="text" icon={<LeftOutlined/>}>{current.product}</Button>
            </Col>  
            <Col xxl={13} sm={16} xl={16} xs={20} className='offset-top-4'>
                <div className={`${resize === false ? null :"steps"}`}>
                    {resize === false ? null : <p className='steps-title'>Progress</p>}
                <Steps  current={2} labelPlacement={'vertical'} direction={resize === false ? "horizontal" :"vertical"} className="orange">
                    {steps.map((item:any)=>(
                        <Step  key={item.title} title={item.title} />
                    ))}
                </Steps>
                </div>
            </Col>
            <Col xxl={13} sm={16} xl={16} xs={24}>
                <div className="current_deal_info">
                    <div className='current_deal_img'>
                        <img  src={current.photo} alt="" />
                    </div>
                    <div className="current_deal_data">
                        <div className="info">
                          <div className="deal_text">
                              <p>Product name</p>
                              <p>{current.product}</p>
                          </div>
                          <div className="deal_text">
                              <p>Link</p>
                              <p>{current.link}</p>
                          </div>
                          <div className="deal_text">
                              <p>Email</p>
                              <p>{current.email}</p>
                          </div>
                          <div className="deal_text">
                              <p>Seller</p>
                              <p></p>
                          </div>
                          <div className="deal_text">
                              <p>NO.</p>
                              <p>{current.id}</p>
                          </div>
                          <div className="deal_text">
                              <p>Year of manufacture</p>
                              <p></p>
                          </div>
                          <div className="deal_text">
                              <p>Color</p>
                              <p></p>
                          </div>
                        </div>
                        <div className='deal_footer'>
                            <div className="deal_text">
                                <p>Price:</p>
                                <p>{current.price}</p>
                            </div>
                        </div>
                     
                    </div>
                </div>
            </Col>
            <Col xxl={13} sm={16} xl={16} xs={24}>
            <div className="deal_next_btn">
                            <button>Continue to payment</button>
                        </div>
            </Col>
        </Row>
        </Suspense>
    )
}