import React from 'react';
import { ResizeProvider } from './context/screen';
import {Route,Switch} from 'react-router-dom';
import { Home, Login, Sign,Deals, CreateDeal, AddedAdditional, Terms,EditProfile,Privacy, ErrorPage} from './Layout';
import { Invite, Language,WriteUs,Messages,Review } from './components';
import message from './assets/img/messages/ic_success.png';
import review from './assets/img/messages/ic_review.png';
import { useWindowSize } from './hooks/useWindowSize';
const App:React.FC = () => {
  
  const resize= useWindowSize();

  
 
  return <ResizeProvider>
    
  <Switch>
        <Route exact path='/' component={Home} />
        <Route path='/login' component={Login} />
        <Route path='/sign_in' component={Sign} />
        <Route path='/deals' component={Deals} />
        <Route path="/create_deal" component={CreateDeal}/>
        <Route path='/add_additional' component={AddedAdditional} />
        <Route path='/terms_of_thecontract' component={Terms} />
        <Route path='/edit/invite' component={Invite} />
        <Route path='/edit/language' component={Language} />
        <Route path="/edit/profile" component={EditProfile} />
        <Route path='/writeus' component={WriteUs} />
        <Route path='/messages' render={()=><Messages background='bg-blue' type='notification' urlBtn='/login' options={{visible:true,src:message}} />}/>
        <Route path='/invite_message' render = { () => <Messages background='bg-blue' type='invite' options={{visible:true, src:message}} />} />
        <Route path='/write_review' component={Review} />
        <Route path='/review' render={()=> <Messages background='bg-blue' type='review' options={{visible:true, src:review}} />} />
        <Route path='/privacy' component={Privacy}  />
        {resize === false ? null : <Route path='/error' component={ErrorPage} />}
        
      </Switch>
      
      </ResizeProvider>
  
}


export default App;