import React,{useState,useEffect,useRef} from 'react';
import { Row,Col,Layout,Button, Space,Typography,Divider,Collapse } from 'antd';
import { ResizeGlobalContext } from '../../context/screen';
import {useHistory} from 'react-router-dom';
import {PlusOutlined,LeftOutlined} from '@ant-design/icons';
import { useDispatch } from 'react-redux';
import { AddedAdditional } from '../../Redux/actions/addedAdditianl';
import './createdeal.scss';
import { useWindowSize } from '../../hooks/useWindowSize';
export const CreateDeal:React.FC = () => {
    const resize = useWindowSize();
    const history = useHistory();
    const dispatch = useDispatch();
    const [formData,setFormData]:any = useState({
        email:'',
        product:'',
        price:'',
        link:'',
        time:'',
        photo:'',
        comment:'',
        id:""
    });
   
    const [buyer,setBuyer] = useState(true);
    const [seller,setSeller] = useState(false);
   
    const chooseRoleBuyerBtn = () => {
        setBuyer(true);
        if(seller === true){
            setSeller(false);
            setBuyer(true)
        }
 
    }
   const chooseRoleSellerBtn = () => {
       setSeller(true)
       if(buyer === true){
        setSeller(true);
        setBuyer(false)
    }

   }
    const {Content} = Layout;
    const {Panel} = Collapse;
    const {Title, Paragraph} = Typography;
    const goBackBtn = () => {
        history.goBack();
    }
    const submitAdditionalForm = (e:React.FormEvent)=>{
        e.preventDefault();
        const iddeal = Math.floor(Math.random() * 10000);
        dispatch(AddedAdditional({...formData,role: buyer === true ? "i'm buying":"i am selling",status:'Pending',id:String(iddeal)}));
    }
    const goToAdditionalPage = () => {
        history.push('/add_additional');
    }
    const goToTermsPage = () => {
        history.push('/terms_of_thecontract')
    }
    const handleChangeInput = (event:any) => {
        const {name,value}:any = event.target;
        setFormData({...formData,[name]:value});
  
    }

    const handleChangeInputPhoto = async (event:any) => {
        const file = event.target.files[0];
        const base64 = await convertBase64(file);
        setFormData({...formData,photo:base64})
    }
    const convertBase64 = (file:any)=>{
        return new Promise((resolve,reject)=>{
            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);
            fileReader.onload = () => {
                resolve(fileReader.result);
            }
            fileReader.onerror = (error) => {
                reject(error);
            }
        })
    }
    return(
       <Layout className='bg-white'>
           <Content className='padding-top-1 padding-left-1 padding-right-1 padding-bottom-1'>
               <Row justify='center'>
                   
                   <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:'start'}}>
                        <Button icon={<LeftOutlined/>} className="dark-blue body-line-normal-w-200" type="text" onClick={goBackBtn}>{resize === false ? 'Back':'New deal'}</Button>
                   </Col>
                   <Col xxl={24} xl={24} sm={24} xs={24} style={resize === false ? {textAlign:'center'} : {textAlign:'center',maxWidth:'480px'}} className='padding-top-1'>
                        <Space direction='vertical'>
                            {resize === false ? <Title className='medium-line-medium-w-300 dark-blue '>New deal</Title> : null}
                            <Title className='normal-line-medium-w-300 dark-blue' style={{padding: "0.5em 0 "}}>Choose your role</Title>
                            <div className='padding-bottom-2'>
                                <button className={`btnrole ${!buyer ? "" : "active"}`} onClick={()=>chooseRoleBuyerBtn()}>I am a buyer</button>
                                <button className={`btnrole ${!seller ? "" : "active"}`} onClick={()=>chooseRoleSellerBtn()} >I am a seller</button>
                            </div>
                            <form onSubmit={submitAdditionalForm}>
                                <Space direction='vertical' size={21}>
                                <div className="group">
                                    <input type="text" name='email' onChange={handleChangeInput} required value={formData.email}/>
                                    <label>Sellers email or phone number</label>
                                </div>
                                <div className="group">
                                    <input type="text" name='product' onChange={handleChangeInput} required value={formData.product} />
                                    <label>Product name</label>
                                </div>
                                <div className="group">
                                    <input type="text" name='price' onChange={handleChangeInput} required value={formData.price} />
                                    <label>Price input</label>
                                </div>
                                <div className="group">
                                    <input type="text" name='link' onChange={handleChangeInput} required value={formData.link} />
                                    <label>Link to item page</label>
                                </div>
                                <div className="group">
                                    <input type="text" name='time' onChange={handleChangeInput} required value={formData.time} />
                                    <label>Delivery time</label>
                                </div>
                                <div className='group'>
                                <input type='file'  name='photo'className='photo' onChange={handleChangeInputPhoto}  required formEncType="multipart/form-data" />
                                    <label className='file'>Add photo</label>
                                </div>
                                <div className='group'>
                                    <input type="text" name='comment' onChange={handleChangeInput} required value={formData.comment} />
                                    <label>Comment</label>
                                    
                                </div>
                                </Space>
                                <Divider style={{marginBottom:'0px'}} />
                               
                                <Collapse className="body-line-normal-w-300 bluedark" ghost={true} onChange={goToAdditionalPage} destroyInactivePanel={true} expandIconPosition='right' bordered={false} accordion={false} expandIcon={()=><PlusOutlined/>}>
                                    <Panel key="1" header ="Add additional information"></Panel>
                                    
                                </Collapse>
                                <Divider style={{marginBottom:'0px',marginTop:"0px"}} />
                                <Collapse  className="body-line-normal-w-300 " ghost={true} onChange={goToTermsPage} destroyInactivePanel={true} expandIconPosition="right" bordered={false} accordion={false}>
                                    <Panel key="1" header="Terms of the contract">

                                    </Panel>
                                </Collapse>
                                <Divider style={{marginTop:"0px"}} />
                                <button className='requestbtn' type='submit'>Send request to { buyer ? `seller` :`buyer`}</button>
                            </form>
                        </Space>
                   </Col>
                  
               </Row>
           </Content>
       </Layout>
    )
}