import React,{useEffect,Suspense} from 'react';
import { useDispatch,useStore} from 'react-redux';

import {DealList,Header} from '../../components/index';
import { Profile,Help,Raiting,CurrentDeal } from '../index';
import {Switch,Route } from 'react-router-dom';
import { Layout, Row, Col, Space,  Avatar, Typography} from 'antd';

import './deals.scss';
import { AllAdditionalAction } from '../../Redux/actions/allAdditional';
import { useWindowSize } from '../../hooks/useWindowSize';


export const Deals:React.FC = () => {
    const resize = useWindowSize();
    const store = useStore();
    const state = store.getState().AdditionalReducer;
    const user = store.getState().UserReducer;
    console.log(user);
    const dispatch = useDispatch();


    const {Content} = Layout;
    const {Title, Paragraph} = Typography;
   

        useEffect(()=>{
            
                dispatch(AllAdditionalAction());
            
        },[dispatch,state])

        
    return (
            <Layout className='bg-white'>
                <Content>
                        <Row justify='center'>
                            <Col xxl={24} xl={24} md={24} lg={24} sm={24} xs={24}>
                                <div className='header-nav'>
                                    <Header />
                                    <main>
                                        <Switch>
                                            <Route path='/deals/deallist'>
                                                     {state.length === 0 ? 
                     <Row justify='center' className={`${resize === false ? 'offset-top-2' : "offset-top-1"}`}>
                            
                         <Col xxl={16} sm={16} xl={16} xs={24} className={`${resize === false ? "" : "padding-left-1 padding-right-1"}`}>
                             <div className='medium-line-medium-w-600 dark-blue'>
                                 Deals
                             </div>
                        </Col>
                         <Col xxl={16} sm={24} xl={24} xs={24} style={{textAlign:'center'}} className='offset-top-8'>
                            <Space direction='vertical'>
                                 <Avatar size={resize=== false ? 270 : 210} src={user.profileObj.imageUrl} />
                                 <Title className='normal-line-normal-w-600 dark-blue'>{user.profileObj.name}</Title>
                                <Paragraph className={`body-line-normal-w-500 dark-blue ${resize === true ? 'padding-left-8 padding-right-8': "padding-left-20 padding-right-20"} `}>Welcome on board! Register new deal</Paragraph>
                            </Space>
                        </Col>
                       
                    </Row> : <Suspense fallback={<div></div>}><DealList data={state}   background="light"/></Suspense>}
                                            </Route>
                                            <Route path='/deals/help' component={Help}/>
                                            <Route path='/deals/profile' component={Profile} />
                                            <Route path='/deals/raiting' component={Raiting} />
                                            <Route path="/deals/:id" component={CurrentDeal} />
                                        </Switch>
                                    </main>
                                </div>
                            </Col>
                        </Row>
                </Content>
            </Layout>
    )
}