import React,{useState,useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation } from "react-i18next";
import img from '../../assets/img/img.png';
import logo from '../../assets/img/logo.png';
import arrow from '../../assets/img/ic_arrow.png';
import phone from '../../assets/img/mockup_2.png';
import phoneDeals from '../../assets/img/mockup_1.png';
import man from '../../assets/img/img-man.png';
import phoneimg from '../../assets/img/mockup_1-phone.png';
import card from '../../assets/img/img-phone-card.png';
import icon from '../../assets/img/icons/ic_language.svg';
import plusIconDark from '../../assets/img/icons/ic_plus_dark.png';
import plusIcon from '../../assets/img/icons/ic_plus.png';
import phone2 from '../../assets/img/mockup_22.png';
import phone3 from '../../assets/img/mockup_3.png';
import phone4 from '../../assets/img/mockup_4.png';
import phone5 from '../../assets/img/mockup_5.png';
import {ListItem} from '../../components';
import {Layout,Row,Col,Image, Typography, Button, Space, Select, Anchor, Carousel} from 'antd';
import './homepage.scss';
import { useWindowSize } from '../../hooks/useWindowSize';


export const Home:React.FC = () => {
    
    const resize = useWindowSize();
    const history = useHistory();
    const {t,i18n} = useTranslation();
    const {Content,Footer} = Layout;
    const {Option} = Select;
    const {Title, Paragraph} = Typography;
    const {Link} = Anchor;
    const [state,setState] = useState(0);
    const Language = (language:string)=>{
        console.log(language);
        
        i18n.changeLanguage(language.toLocaleLowerCase());
    };
    console.log(i18n);
    useEffect(()=>{},[t]);
    const data =[{
        
        title:t("row-tree.list.title-one"),
        description:t("row-tree.list.paragraph-one")
    },
{
    title:t("row-tree.list.title-two"),
    description:t("row-tree.list.paragraph-two")
},
{
    title:t("row-tree.list.title-tree"),
    description:t("row-tree.list.paragraph-tree")
}
]
    const dataWorks = [
        {title:t("row-four.list.title-one")},
        {title:t("row-four.list.title-two")},
        {title:t("row-four.list.title-tree")},
        {title:t("row-four.list.title-four")},
        {title:t("row-four.list.title-five")}
    ]

    const goToLoginPage = () => {
        history.push('/login');
    }
    const btnElement = (className:string) => {
        return (<Button size='large' type='ghost' onClick={goToLoginPage} shape='round' className={className}>
        {t('button.title')}
    </Button>)
    }
    return (
        <Layout className="bg-white">
            <Anchor affix={false} style={{paddingLeft:'0px'}}>
            <Content>
                <Row  className=" padding-top-1 padding-bottom-1 padding-left-1 padding-right-1 bg-dark-blue" justify='space-between'>
                    <Col xxl={8} >
                            <Space direction='vertical'  size='large' style={{justifyContent:'space-between',height:'100%'}}>
                                <Image preview={{visible:false, mask:false}} src={logo} />
                                <Space direction='vertical' size='large' className={`center-media ${resize === false ? '' : 'padding-bottom-5 padding-top-2'}`}>
                            <Title className={`${resize === false ? '' : 'padding-left-1 padding-right-1'}   white extralarge-line-extralarge-w-600`}>
                                {t('row-one.title')}
                            </Title>
                            <Paragraph className='orange normal-line-medium-w-500 '>
                               {t('row-one.paragrapgh')}
                            </Paragraph>

                                {btnElement(`white btn-dark ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                                </Space>
                                <Link href='#what' title={<Image  preview={{visible:false, mask:false}} src={arrow}   />} className='unvisible' />
                                
                            </Space>                   
                    </Col>
                    <Col xxl={11} lg={24} md={24} xl={24} >

                        <Image wrapperStyle={{width:'100%'}} preview={resize === false ? {visible:false, mask:false} : {}} src={img} />
                    </Col>
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center',position:'absolute',zIndex:2,top:"24pt", gap:"8pt",right:0}}>
                        
                            <Image preview={{visible:false, mask:false }}  src={icon} />
                        
                        <Select dropdownMatchSelectWidth={120}  onChange={(value)=>Language(value)}  defaultValue="EN" className='white body-line-normal-w-700 selects custom-width' bordered={false} showArrow={false} dropdownStyle={{borderRadius:"10%",width:"120px"}}>
                            
                            <Option value='EN'>EN</Option>
                            <Option value="LT">LT</Option>
                            <Option value="RU">RU</Option>
                            
                        </Select>
                        </div>
                </Row>
                <Row id='what' className="white offset-top-6 offset-bottom-5" justify='center'>
                    <Col xxl={10} lg={12} md={12} xl={12} className='order-2'>
                        <Space size="large" className=' padding-top-2 padding-bottom-2 padding-left-2 padding-right-2'>
                            <Space className="padding-top-5">
                                <Image preview={{visible:false, mask:false}} src={phone} />
                            </Space>
                            <Space className="padding-bottom-5">
                                <Image preview={{visible:false, mask:false}} src={phoneDeals} />
                            </Space>
                       
                        </Space>

                        {btnElement(`btn-light display-media-screen auto offset-top-1 ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                    </Col>
                    <Col xxl={8} xl={12} lg={12} md={12} style={{alignSelf:"center"}}>
                        <Space direction='vertical' size='large' className='padding-left-1 padding-right-1' style={{width:"100%"}}>
                        <Title className="dark-blue large-line-large-w-600  title-response">
                            {t('row-two.title')}
                        </Title>
                        <Space direction='vertical'>
                            <Paragraph className="dark-blue body-line-normal-w-500">
                                {t("row-two.list.paragraph-one")}
                            </Paragraph>
                            <Paragraph className="dark-blue body-line-normal-w-500">
                            {t("row-two.list.paragraph-two")}
                            </Paragraph>
                            <Paragraph className="dark-blue body-line-normal-w-500">
                            {t("row-two.list.paragraph-three")}
                            </Paragraph>
                        </Space>
                        {btnElement(`btn-light  display-media-none ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                        </Space>
                    </Col>
                </Row>

                <Row justify='space-between' className='offset-top-7 offset-bottom-5 padding-left-1 padding-right-1'>
                    <Col xxl={11} xl={11} lg={24} md={24} sm={24} xs={24} className='bg-orange margin-media border-screen ' style={{borderRadius:'0 120pt 0 120pt',textAlign:'center',}}>
                        <Space direction='vertical' size='large' style={{height:'100%',justifyContent:'center',width:'75%',textAlign:'left'}} className={`responsewidth padding-top-6 padding-bottom-7 ${resize=== false ? "" : 'padding-left-2 padding-right-2'} `} >
                        <Title className='dark-blue large-line-large-w-600'>
                                {t("row-tree.title")}
                            </Title>
                            
                            <ListItem data={data} className='circle' titleClass={`dark-blue ${resize === false ?'normal-line-medium-w-500' : "titles padding-right-3"}`} textClass="dark-blue offset-top-1 body-line-normal-w-500"/>
                            <Space direction='horizontal' className='center'>
                            {btnElement(`btn-light  ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                            </Space>
                        </Space>
                    </Col>
                    <Col xxl={11} xl={12} lg={24} md={24} sm={24} className='margin-media' >
                        <Image preview={{visible:false, mask:false}} src={man} style={{height:"100%"}} wrapperStyle={{width:'100%',height:"100%"}} />
                    </Col>
                </Row>
                <Row justify='space-around' className='offset-top-5 offset-bottom-8'>
                    <Col xxl={10} lg={10} md={10} xl={10} className='order-2' style={{textAlign:'center'}}>
                       
                            
                            <Carousel dots={false} ref={ref=>(ref?.goTo(state))} >
                                <div>
                                    <Space direction='vertical' align='center' className='padding-top-2 padding-bottom-2 padding-left-2 padding-right-2' >
                                    <Image  preview={{visible:false, mask:false}}  src={phoneimg} />
                                    </Space>
                                </div>
                                <div>
                                <Space direction='vertical' align='end' className='padding-top-2 padding-bottom-2 padding-left-2 padding-right-2' >
                                    <Image  preview={{visible:false, mask:false}} src={phone2} />
                                    </Space>
                                </div>
                                <div>
                                <Space direction='vertical' align='end' className='padding-top-2 padding-bottom-2 padding-left-2 padding-right-2' >
                                    <Image  preview={{visible:false, mask:false}} src={phone3 } />
                                    </Space>
                                </div>
                                <div>
                                <Space direction='vertical' align='end' className='padding-top-2 padding-bottom-2 padding-left-2 padding-right-2' >
                                    <Image  preview={{visible:false, mask:false}} src={phone4 } />
                                    </Space>
                                </div>
                                <div>
                                <Space direction='vertical' align='end' className='padding-top-2 padding-bottom-2 padding-left-2 padding-right-2' >
                                    <Image  preview={{visible:false, mask:false}} src={phone5} />
                                    </Space>
                                </div>
                                
                            </Carousel>
                           
                        {btnElement(`btn-light  display-media-screen auto ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                    </Col>
                    <Col xxl={10} xl={10} lg={12} md={24} xs={24}>
                    <Space direction='vertical' size='large' style={{height:'100%', justifyContent:'center',width:'100%'}} className='padding-left-1 padding-right-1' >
                        <Title level={2} className='dark-blue large-line-large-w-600 padding-bottom-3 ' style={resize === false ? {textAlign:'left'}:{textAlign:"center"}}>
                            {t("row-four.title")}
                        </Title>
                    <ListItem data={dataWorks} center={true} slide={setState} click={true} className='circle' titleClass={`${ resize === false ? 'normal-line-medium-w-500' : 'titles'}`} textClass="small line-normal w-200" hover={true} />

                            </Space>
                        {btnElement(`btn-light display-media-none ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                    </Col>
                </Row>

                <Row className='bg-grey'>
                <Row justify='end' style={{width:'100%'}} className="offset-top-5 offset-bottom-5 padding-right-1 padding-left-1" >
                    
                    <div className="worth-container">
                        <div className='worth-item-text'>
                            <h3>{t("row-five.title")}</h3>
                            <p>{t("row-five.paragraph")}</p>
                            {btnElement("worth-btn")}
                        </div>
                        <div className='worth-item'>
                            <div className='bg-blue'>
                                <ul className='worth-items-list minus dark-blue'>
                                    <h4>{t("row-five.list-blue.title")}</h4>
                                    <li>{t("row-five.list-blue.list-item.one")}</li>
                                    <li>{t("row-five.list-blue.list-item.two")}</li>
                                    <li>{t("row-five.list-blue.list-item.tree")}</li>
                                </ul>
                            </div>
                            <div className='bg-dark-blue'>
                                <ul className='worth-items-list plus white'>
                                    <h4 className='white'>{t("row-five.list-dark-blue.title")}</h4>
                                    <li>{t("row-five.list-dark-blue.list-item.one")}</li>
                                    <li>{t("row-five.list-dark-blue.list-item.two")}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    


                        {btnElement(`btn-light  display-media-screen auto offset-top-2 ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                        
                    
                </Row>
                <Row justify='space-between' className='padding-left-1 padding-right-1' style={{width:'100%',gap:"0pt"}} >
                    <Col xxl={11} xl={11} lg={24} md={24} className={`bg-dark-blue order-2 ${resize === false ? "padding-left-9" : "padding-top-6 padding-left-5 padding-right-5 padding-bottom-6"}  bordered-box-reverse`} flex='auto'  >
                        <Space direction='vertical' className='aligment'  style={{justifyContent:'center',height:'100%'}} size='large'>
                        <Space direction='vertical' className='texts' style={{width:'100%'}}> 
                            <Title  className="white extralarge-line-extralarge-w-600 footer-response">
                                {t("row-six.title")}
                            </Title>
                            <Paragraph className={`orange ${resize=== false ? "normal-line-medium-w-500" : 'response-text-footer'}`}>
                                {t("row-six.paragraph")}
                            </Paragraph>
                            {btnElement(`btn-dark offset-top-2 ${i18n.language === 'ru' ? 'btn-ru' : 'body-line-normal-w-500'}`)}
                        </Space>
                        </Space>
                    </Col>
                    <Col xxl={12} xl={12} lg={24} md={24}>
                    <Image  preview={{visible:false, mask:false}} src={card} style={{height:"100%"}} wrapperStyle={{width:'100%',height:"100%"}} />
                    </Col>
                </Row>
                </Row>
                <Footer className={`${resize=== false ? "pad" : "padding-footer"}`}>
                    <Row justify='space-between' style={{alignItems:'baseline',gap:'14px'}} className='center-media-footer' >
                    <Col xxl={11} md={11} sm={11} xs={24} style={{justifyContent:'space-between'}} className='order-2' >
                        
                            <Paragraph className='text-footer dark-blue'>
                            {t("footer.right")}
                            </Paragraph>
  
                        
                    </Col>
                    <Col xxl={11} md={11} sm={11} xs={24} className='text-right'>
                    <Button type='link' className='link-btn text-footer offset-bottom-1'>
                                {t("button.link")}
                            </Button>
                           
                    </Col>
                    </Row>
                </Footer>
            </Content>
            </Anchor>
        </Layout>

    )
}






