import React,{useEffect} from 'react';
import {Layout,Typography,Row,Col} from 'antd';
import { useHistory } from 'react-router';
import { useTranslation } from 'react-i18next';
import './error.scss';
import {Link} from 'react-router-dom';
export const ErrorPage:React.FC = () => {
    const {t} = useTranslation();
    const history = useHistory();
    const {Content} = Layout;
    const {Title,Paragraph} = Typography;
    const GoBack = () => {
        history.goBack();
    }

    useEffect(()=>{},[t])



    return (
       <Layout className='bg-white'>
           <Content>
               <Row justify="center" style={{textAlign:'center'}}>
                   <Col xxl={24} xl={24} xs={24} >
                       <div className='spacing'>
                        <div className='spacing-item'><Title className='dark-blue error-title'> {t("error.title")}</Title></div>
                        <div className='spacing-item'><Paragraph className='dark-blue error-paragraph'>{t("error.paragraph")}</Paragraph></div>
                        <div className='spacing-item'><button className='btn-error' onClick={GoBack}>Go back to list</button></div>
                       </div>
                   </Col>
                   <Col xxl={24} xs={24}>
                        <div className='cookie-msg'>
                            <div className='cookie-msg-item'>
                                <Paragraph className='cookie-msg-title dark-blue'>
                                    {t("error.message.paragraph")} <Link to='/more' className='cookie-msg-link'>{t("error.message.more")}</Link>
                                </Paragraph>
                            </div>
                            <div className='cookie-msg-item'>
                                <button className="cookie-msg-btn">
                                    {t("error.message.btn")}
                                </button>
                            </div>
                            <div className='cookie-msg-item'>
                                <Link to='/slapuku' className='cookie-msg-link'>{t("error.message.link")}</Link>
                            </div>
                        </div>
                   </Col>
               </Row>
           </Content>
       </Layout>
    )
}