import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import {Row,Col,Typography,Button,Space,Avatar,Divider,Rate} from 'antd';
import {Comment} from '../../components';
import './raiting.scss';
import { useStore } from 'react-redux';


export const Raiting:React.FC = () => {
    const state = useStore().getState().UserReducer;
    const {Title} = Typography;
    const {resize} = ResizeGlobalContext();
    return (
        <>
        <Row justify='center' className={`${resize === false ? 'padding-top-2':'padding-top-1'} padding-bottom-3 padding-left-1 padding-right-1 profilebg`} >
            <Col xxl={16} sm={16} xl={16} xs={24}>
                <div className='medium-line-medium-w-300 dark-blue'>
                    Your raiting
                </div>
                <Row justify='space-between' className='offset-top-2' style={resize === false ? {} : {flexDirection:'column'}}>
                    <Row justify='space-between'>
                        <Col xxl={24} xs={24} style={resize === false ? {} : {textAlign:'center',paddingBottom:'2.9em'}}>
                        <Avatar size={210} src={state.profileObj.imageUrl}/>
                        <div>

                        </div>
                        </Col>
                    </Row>
                    <Row justify='space-between'>
                        <Col xxl={24} xs={24}>
                        <Space direction='vertical'>
                        <div className='small-line-body-w-400 dark-blue'>
                            Excellent
                        </div>
                        <div className='raiting dark-blue'>
                            4.5
                        </div>
                        <div>
                            <Rate disabled defaultValue={4} className='orange' />
                        </div>
                        </Space>
                        </Col>
                    </Row>
                </Row>
            </Col>
        </Row>
        <Row justify='center' className='padding-top-2 padding-left-1 padding-right-1'>
            <Col xxl={16} sm={16} xl={16} xs={24}>
                <Row justify='start'>
                    <Title className='dark-blue title-review'>Review</Title>
                    <Comment />
                    <Divider  />
                </Row>
            </Col>
        </Row>
        </>
    )
}