export enum ActionsType {
    ADD_ADDITIONAL = "ADD_ADDITIONAL",
    ALL_ADDITIONAL = "ALL_ADDITIONAL",
    SIGNING_USER = "SIGNING_USER",
}


export interface AddedInformation{
    type: typeof ActionsType.ADD_ADDITIONAL,
    payload:any
}
export interface AllAdditional{
    type: typeof ActionsType.ALL_ADDITIONAL,
}

export interface SigningUser{
    type: typeof ActionsType.SIGNING_USER,
    payload:any
}

export type AdditionalType = 
AddedInformation |
AllAdditional |
SigningUser;