import React,{useState} from 'react';
import { Row, Col, Layout, Typography,Button,Space } from 'antd';
import { LeftOutlined } from '@ant-design/icons';
import {useHistory} from 'react-router-dom';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Invite:React.FC = () => {
    const resize = useWindowSize();
    const [invite,setInvite] = useState({invite:""})
    const {Content}  = Layout;
    const {Title} = Typography;
    const history = useHistory()
    const goBack = () => {
        history.goBack();
    }
    const inviteFriendForm = (event:React.FormEvent) => {
        event.preventDefault();
        history.push('/invite_message');
    }
    const handleChange = (event:any) =>{
        const {name,value} = event.target;
        setInvite({...invite,[name]:value});
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} sm={24} xs={24} style={{textAlign:'start'}}>
                        <Button icon={<LeftOutlined />} className='dark-blue body-line-normal-w-200' type='text' onClick={goBack} >{resize===false ? 'Back':'Invite friend'}</Button>
                    </Col>
                    <Col xxl={24} style={resize === false ? {textAlign:'center'}:{textAlign:'center',maxWidth:'480px'}} className="padding-top-2">
                        <Space direction='vertical' >
                            {resize === false ?<Title className='medium-line-medium-w-500 dark-blue padding-bottom-2'>
                                Invite a friend
                            </Title>:null}
                            <form onSubmit={inviteFriendForm}>
                                <Space direction='vertical' size='small'>
                                <div className='group'>
                                        <input onChange={handleChange} type='text' name='invite' required />
                                        <label>Email</label>
                                </div>
                                <button className='requestbtn offset-top-3' type='submit'>Send invitation</button>
                                </Space>
                            </form>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}