import React,{useEffect} from 'react';
import { Row,  Layout, Image } from 'antd';
import { useTranslation } from 'react-i18next';
import './privacy.scss';
import slide from '../../assets/img/icons/ic_slide.png';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Privacy:React.FC = () => {
    const resize = useWindowSize();
    const {Content} = Layout;
    const {t} = useTranslation();

    useEffect(()=>{},[t]);
    const items:any = t("privacy.content.row-two.list-items",{returnObjects:true});
    const items_row:any = t("privacy.content.row-three.list-titles",{returnObjects:true});
    const items_five:any = t("privacy.content.row-five.items",{returnObjects:true});
    const table:any = t("privacy.table.titles",{returnObjects:true});
  console.log(table);

    return (
        <Layout className='bg-white'>
            <Content>
                <Row justify='center'>
                    <div className="privacy-container">
                            <div className="privacy-item">
                                <h2 className='privacy-title'>{t("privacy.title")}</h2>
                            </div>
                            <div className="privacy-item">
                                <p className='privacy-paragraph'>{t("privacy.content.row-one.title")}</p>
                                <p className='privacy-text'>
                                {t("privacy.content.row-one.paragraph")}
                                </p>
                            </div>
                            <div className="privacy-item">
                                <p className="privacy-paragraph">
                                    {t("privacy.content.row-two.title")}
                                </p>
                                <ul className='privacy-list'>
                                    <p className='privacy-text'>{t("privacy.content.row-two.list-title")}</p>
                                    {items.map((e:any,index:any)=>(
                                        <li key={index} className='privacy-list-item'>{e}</li>
                                    ))}
                                </ul>
                            </div>
                            <div className="privacy-item">
                                <p className="privacy-paragraph">
                                    {t("privacy.content.row-three.title")}
                                </p>
                                {items_row.map((e:any,index:any)=>(
                                    <ul key={index} className="privacy-list">
                                        <li className="privacy-list-item">{e.title}</li>
                                        <p className="privacy-text margin">{e.paragraph}</p>
                                    </ul>
                                ))}
                            </div>
                            <div className="privacy-item">
                                <p className="privacy-paragraph">
                                    {t("privacy.content.row-four.title")}
                                </p>
                                <p className="privacy-text">
                                    {t("privacy.content.row-four.paragraph")}
                                </p>
                            </div>
                            <div className="privacy-item">
                                <p className="privacy-paragraph">
                                    {t("privacy.content.row-five.title")}
                                </p>
                                <ul className="privacy-list">
                                    {items_five.map((e:any,index:any)=>(
                                        <li key={index} className="privacy-list-item">
                                            {e}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                            <h3 className='privacy-table-title'>
                                {t("privacy.table.title")}
                                </h3>
                               
                                {resize === false ? null :<div style={{width:"100%",textAlign:"center",marginTop:"36pt"}}> <Image preview={{visible:false, mask:false}} src={slide}/></div>}
                                
                            <div className="privacy-table">
                                
                                <table>
                                  
                                    <thead>
                                        <tr>
                                            {table.map((e:any,index:any)=>(
                                                <th scope='col' key={index}>{e.title}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                            {table.map((e:any,index:any)=>(
                                                <tr key={index}>
                                                    <th scope="row">{e.paragraphs[0]}</th>
                                                    <td>{e.paragraphs[1]}</td>
                                                    <td>{e.paragraphs[2]}</td>
                                                    <td>{e.paragraphs[3]}</td>
                                                    <td>{e.paragraphs[4]}</td>
                                                </tr>
                                            ))}
                                    </tbody>
                                </table>
                            </div>
                    </div>
                </Row>
            </Content>
        </Layout>
    )
}