import React,{useState} from 'react';
import {Row,Col, Layout,Typography,Button,Divider,Checkbox} from 'antd';
import { useHistory } from 'react-router-dom';
import { LeftOutlined } from '@ant-design/icons';
import './language.scss';
import english from '../../assets/img/icons/english_flag.png';
import russian from '../../assets/img/icons/russian_flag.png';
import lith from '../../assets/img/icons/lithuanian_flag.png';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Language:React.FC = () => {
    const resize = useWindowSize();
    const {Content} = Layout;
    const {Title} = Typography;
    const history = useHistory();
    const goBack = () => {
        history.goBack();
    }
    const [language,setLanguage]:any = useState({
        english:true,
        lithuanian:false,
        russian:false
    });
    const changeLanguage = (event:any) => {
        const {name,checked} = event.target;
        setLanguage({[name]:checked})
    }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-left-1 padding-right-1'>
                <Row justify='center'>
                    <Col xxl={24} xl={24} xs={24} style={{textAlign:'start'}}>
                        <Button className='dark-blue body-line-normal-w-200' type='text' onClick={goBack} icon={<LeftOutlined/>}>
                            {resize === false ?'Back':'Language'}
                            
                            </Button>
                    </Col>
                    <Col xxl={6} style={{textAlign:'center',maxWidth:"480px",width:"100%" }} className='padding-top-2'>
                            {resize === false ?
                            <Title className='medium-line-medium-2-500 dark-blue'>
                                Language
                            </Title>:null
}
                            <Divider/>
                            <div className='radiolanguage'>
                                <div>
                                    <img alt='english' src={english} />
                                    <label>English</label>
                                </div>
                                <Checkbox onChange={changeLanguage} checked={language.english} name='english'  />
                            </div>
                            <Divider/>
                            <div className='radiolanguage'>
                                <div>
                                    <img alt='englis' src={lith} />
                                    <label>Lietuvių</label>
                                </div>
                                <Checkbox onChange={changeLanguage} checked={language.lithuanian} name='lithuanian' />
                            </div>
                            <Divider/>
                            <div className='radiolanguage'>
                                <div>
                                    <img alt='englis' src={russian} />
                                    <label>Русский</label>
                                </div>
                                <Checkbox checked={language.russian} onChange={changeLanguage} name='russian' />
                            </div>
                            <Divider/>
                            <button className='requestbtn offset-top-2'>Save changes</button>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}