import React,{useEffect,useState} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import {Row,Col,Typography,Button,Space,Avatar,Collapse,Divider} from'antd';
import { useHistory } from 'react-router-dom';
import './help.scss';




export const Help:React.FC = () => {
    const history = useHistory();
    const {resize} = ResizeGlobalContext();
    const {Title,Paragraph} = Typography;
    const {Panel} = Collapse;
    const goUsBtn = () => {
        history.push('/writeus')
    }
    return (
        <>
            <Row justify='center' className={`${resize === false ? "padding-top-2" : "padding-top-1"} padding-bottom-3 padding-left-1 padding-right-1 profilebg`}>
                <Col xxl={16} sm={16} xl={16} xs={24}>
                    <div className='medium-line-medium-w-300 dark-blue padding-bottom-3'>
                        Help
                    </div>
                    <Paragraph style={{margin:'0'}} className='normal-line-medium-w-300 dark-blue'>Start conversation</Paragraph>
                    <Paragraph style={{margin:"0"}} className="small-line-body-w-200 dark-blue">Usually responds in less than 20 minutes.</Paragraph>
                    <Avatar.Group style={{alignItems:'center'}} className='offset-top-2'>
                        <Avatar size='large' />
                        <Avatar size='large' />
                        <button onClick={goUsBtn} className='writeusbtn'>write to us</button>
                    </Avatar.Group>
                </Col>
            </Row>
            <Row justify='center' className='padding-top-1'>
                <Col xxl={16} sm={16} xl={16} xs={24}>
                <Collapse  style={{fontSize:"18pt",lineHeight:"24pt", fontFamily:"'DM Sans', sans-serif",fontWeight:300}} ghost={true} expandIconPosition='right' bordered={false} accordion={false} >
                    <Panel className='panelcolapse' extra={"16 questions in this collection"} key='1' header='Excepteur sintlore oceca'>
                        
                    </Panel>
                </Collapse>
                <Divider style={{marginTop:"0px"}}/>
                </Col>
            </Row>
        </>
    )
}