import React,{useState,useEffect} from 'react';
import { ResizeGlobalContext } from '../../context/screen';
import GoogleLogin from 'react-google-login';
import FacebookLogin from 'react-facebook-login';
import AppleSignin from 'react-apple-signin-auth';
import MicrosoftLogin from "react-microsoft-login";
import { Row, Col, Layout, Divider,Typography,Space,Button, Image, Checkbox } from 'antd';
import closeIcon from '../../assets/img/icons/ic_close.png';
import {useHistory} from 'react-router-dom';
import './login.scss';
import fb from '../../assets/img/icons/ic_fb.png'
import google from '../../assets/img/icons/ic_google.png';
import { UserAction } from '../../Redux/actions/userActions';
import { useDispatch } from 'react-redux';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Login:React.FC = () => {
    const dispatch = useDispatch();
    const [remember,setRemember] = useState(false);
    const resize = useWindowSize()
    const history = useHistory();
    const {Content} = Layout;
    const {Title, Paragraph} = Typography;
    const homePage=()=>{
        history.push('/');
    }
    const goToSignIn =() => {
        history.push('/sign_in');
    }
    const CheckboxGroup = Checkbox.Group;
    const authHandler = (err, data) => {
        console.log(err, data);
      };

      const responseLogin = (response) => {
          if(response){
              dispatch(UserAction(response));
              history.push('/deals/deallist');
          }else{
              return false;
          }
      }
    return (
        <Layout className='bg-white'>
            <Content className='padding-top-1 padding-bottom-1 padding-right-1 padding-left-1' >
                <Row justify='center'>
                    <Col xxl={24} xs={24} className='offset-bottom-3' style={{textAlign:'end'}}>
                        
                        <Image preview={{visible:false,mask:false}} onClick={homePage} style={{cursor:'pointer'}} src={closeIcon} />
                        
                    </Col>
                    <Col xxl={24} xs={24} style={{textAlign:'center'}}>
                        <Title className='dark-blue large-line-large-w-600'>
                            Log in
                        </Title>
                        <Space direction='horizontal' className='offset-top-1' align='baseline' style={{gap:"4px"}}>
                        <Paragraph className='normal-line-body-w-500 dark-blue'>
                            Don't have an account? 
                        </Paragraph>
                        <Button type='link' className='normal-line-body-w-500 blue padding-zero' onClick={goToSignIn}>
                            Sign up
                        </Button>
                        </Space>
                    </Col>
                </Row>
                <Row justify='center' className='offset-top-2'>
                    <Col xxl={6} xs={24} style={resize === false ?{}:{maxWidth:"480px"}}>
                        
                        <form >
                            <Space direction='vertical' size='large'>
                            <div className='group'>
                                <input type="text" required />
                                <label>Email or phone number</label>
                            </div>
                            <div className='group'>
                                <input type="text" required />
                                <label>Password</label>
                            </div>
                            
                            <Space direction='horizontal' className='offset-top-1' style={{justifyContent:'space-between',width:'100%'}}>
                            
                            <div className='flex-checkbox'>
                            <input type="checkbox" className='winwincheckbox' name='winch' />
                            <label htmlFor='winch' className='body-line-normal-w-300 dark-blue'>Remember me</label>
                            </div>
                            <Button type='link' className='body-line-normal-w-300'>
                                Forgot password?
                            </Button>
                            </Space>
                                <Button className='login-btn offset-top-2 offset-bottom-2'>
                                    log in
                                </Button>
                            </Space>
                        </form>
                        
                    </Col>
                    <Col xxl={5} xs={24} style={{textAlign:'center'}}>
                        <Divider type={resize === false ? 'vertical' :'horizontal'} plain style={{height:`${resize === false ? "100%" : '15%'}`,width:'5px'}}>{resize === false ? '' : <Paragraph className='dark-blue' style={{fontSize:'16pt',lineHeight:'20pt',fontFamily:'DM Sans, sans-serif'}}>Or log in with</Paragraph>}</Divider>
                    </Col>
                    <Col xxl={6} xs={24}>
                        <Space direction={resize === false ? 'vertical' : 'horizontal'} style={{justifyContent:`${resize === false ? 'space-around' :'center'}`,width:'100%'}} size='large'>
                            <GoogleLogin 
                            onSuccess={responseLogin}
                            clientId='361301667370-tcbp8u1lihkvkn58fq1jouq3u5dhegmv.apps.googleusercontent.com'  children={<Space direction='horizontal'  align='center' style={{justifyContent:`${resize === false ?'space-around' : 'center'}`,display:'flex',flexDirection:'row',gap:`${resize === false ? '8px' :'0px'}`}}><img className='googleIcon' src={google} alt='' /> {resize === false ? <p>continue with google</p> : undefined}</Space>} icon={false} theme='dark'  className='btn-login body-line-normal-w-500 white google'  />
                            <FacebookLogin cssClass='btn-login facebook white body-line-normal-w-500' textButton={resize === false ?'continue with facebook' : ''} icon={<img src={fb} alt='' />}  appId = '11545454' callback={()=>{}} />
                            <AppleSignin authOptions={{clientId:'com.example.web'}} buttonExtraChildren={resize === false ? "Continue with Apple" : ''} className="btn-login body-line-normal-w-500 apple" style={{paddingRight:`${resize === false ? '8px' : '0px'}`}} />
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}