import React,{useState} from 'react';
import { useHistory } from 'react-router-dom';
import { Row, Col, Layout, Divider,Typography,Space,Button, Image, Checkbox } from 'antd';
import closeIcon from '../../assets/img/icons/ic_close.png';
import { ResizeGlobalContext } from '../../context/screen';
import './signin.scss';
import { useWindowSize } from '../../hooks/useWindowSize';
export const Sign:React.FC = () => {
    const resize = useWindowSize();
    const {Content} = Layout;
    const history = useHistory()
    const [remember,setRemember] = useState(false);
    const {Title, Paragraph} = Typography;
    const CheckboxGroup = Checkbox.Group;
    const goToHomePage = () => {
            history.push('/');
    }
    const goToLogIn = () => {
        history.push('/login')
    }
    const signinbtn = () => {
        history.push('/messages')
    }
    const goToPrivacy = () => {
        history.push('/privacy');
    }
    return (
        <div>
            <Layout className='bg-white'>
                <Content className='padding-top-1 padding-bottom-1 padding-right-1 padding-left-1' >
                <Row justify='center'>
                    <Col xxl={24} xs={24} className='offset-bottom-3' style={{textAlign:'end'}}>
                        
                        <Image preview={{visible:false,mask:false}} onClick={goToHomePage} style={{cursor:'pointer'}} src={closeIcon} />
                        
                    </Col>
                    <Col xxl={24} xs={24} style={{textAlign:'center'}}>
                        
                        <Title className='dark-blue large-line-large-w-600'>
                            Sign up
                        </Title>
                        <Space direction='horizontal' className='offset-top-2' size={0} align='baseline'  style={{gap:"4px"}}>
                        <Paragraph className='normal-line-body-w-500 dark-blue padding-half'>
                            Already have an account?
                        </Paragraph>
                        <Button type='link' className='normal-line-body-w-500 blue padding-zero' onClick={goToLogIn}>
                            Log in
                        </Button>
                        
                        </Space>
                    </Col>
                </Row>
                <Row justify='center' className='offset-top-1' style={resize === false ? {textAlign:'center'}:{textAlign:'center',maxWidth:"480px"}}>
                    <Col xxl={24} >
                        <form>
                            <Space direction='vertical' size='middle'>
                            <div className='group'>
                                <input type="text" required />
                                <label>First name</label>
                            </div>
                            <div className='group'>
                                <input type="text" required />
                                <label>Last name</label>
                            </div>
                            <div className='group'>
                                <input type="text" required />
                                <label>Email</label>
                            </div>
                            <div className='group'>
                                <input type="text" required />
                                <label>Phone number</label>
                            </div>
                            <Space direction='horizontal' style={{width:'100%'}}>
                            <div className='flex-checkbox'>
                            <input type="checkbox" className='winwincheckbox' name='winch' />
                            <label htmlFor='winch' className='body-line-normal-w-300 dark-blue'>I agree with</label><Button type='link' onClick={goToPrivacy} className='body-line-normal-w-300 padding'>Privacy policy</Button>
                            </div>
                            </Space>
                                <Button className='signin-btn offset-top-1' onClick={signinbtn}>
                                    Sign up
                                </Button>
                            
                            </Space>
                            
                        </form>
                    </Col>
                </Row>
                </Content>
            </Layout>
        </div>
    )
}