import React,{useEffect} from 'react';
import {List, Typography} from 'antd';
import { ListItemsProps } from '../../Types/index';


export const ListItem:React.FC<ListItemsProps> = ({className,data,titleClass,textClass,hover,click,slide,center}) => {
    const {Title, Paragraph} = Typography;
    const clickToSlide = (index:any) => {
        slide(index);
    }
    useEffect(() => {

    }, [className])
    return(
        <List itemLayout='horizontal' split={false} dataSource={data} renderItem={(item:any,index)=>(
        <List.Item onClick={click === true ? ()=>clickToSlide(index) : ()=>{} } >
            <List.Item.Meta  className={`${center === true ? 'center-li' : ''} ${hover === true ? 'hover' : ''}`} avatar={<div className={`${className}`}>{index + 1}</div>}  title={<Title className={`${titleClass}`} level={3}>{item.title}</Title>} description={<Paragraph className={textClass}>{item.description}</Paragraph>}/>
        </List.Item>)} />
    )
}


