import {useState,useEffect, createContext,useContext } from "react";

const ResizeScreenContext:any = createContext({});



export const ResizeProvider = ({children})=>{
    const [resize,setResize] = useState(false);
    const handleResize = () => {
        if(window.innerWidth <= 501){
            setResize(true);
        }else{
            setResize(false);
        }
    }
    useEffect(()=>{
      
        handleResize()
        window.addEventListener('resize',handleResize);
        return ()=>{
            window.removeEventListener('resize',handleResize);
        }
    },[])

    return (
        <ResizeScreenContext.Provider value={{resize}}>
            {children}
        </ResizeScreenContext.Provider>
    )
}

export const ResizeGlobalContext = () =>  {
    
   const {resize} = useContext(ResizeScreenContext);
    return {resize};
}